import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
    routes: [
        { path: '/', redirect: '/basic' },
        { path: '/Basic', component: () => import('./views/Basic.vue') },
        { path: '/DeeplyNestedMenu', component: () => import('./views/DeeplyNestedMenu.vue') },
        { path: '/ContextMenu', component: () => import('./views/ContextMenu.vue') },
        { path: '/Theme_metal', component: () => import('./views/Theme_metal.vue') },
        { path: '/Theme_black', component: () => import('./views/Theme_black.vue') },
        { path: '/CustomizableTheme', component: () => import('./views/CustomizableTheme.vue') },
        { path: '/NonTextMenuItem', component: () => import('./views/NonTextMenuItem.vue') },
    ]
})